import React, { useContext, useState } from "react";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import TextInput from "../../../../ui/TextInput/TextInput";
import TextEditor from "../../../../ui/TextEditor/TextEditor";
import DropZone from "../../../../ui/DropZone/DropZone";
import { convertToRaw, EditorState, convertFromRaw } from "draft-js";
import SelectMenu from "../../../../ui/SelectMenu/SelectMenu";
import { Resolutions, maxViewCounts, FPSs } from "../../../../constants/Video";
// import CustomCheckBox from "../../../../ui/CustomCheckBox/CustomCheckBox";
import { CourseContext } from "../../../../context/CourseContext/CourseContext";

const useStyles = makeStyles((theme) => ({
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function SectionContentForm(props) {
  const classes = useStyles();
  const {
    addCourseModuleContent,
    updateCourseModuleContent,
    uploadingProgress,
  } = useContext(CourseContext);
  const {
    title,
    description,
    type,
    action,
    id,
    courseModule,
    setOpen,
    file,
    max_view_count,
  } = props;
  const [ttl, setTitle] = useState(title ? title : "");
  const [desc, setDesc] = useState(description ? description : "");
  const [maxViewCount, setMaxViewCount] = useState(
    max_view_count ? max_view_count : 2
  );
  // const [resolution, setResolution] = useState(0);
  // const [fps, setFPS] = useState(0);
  // const [download, setDownload] = useState(false);
  const [content, setContent] = useState(
    file
      ? EditorState.createWithContent(convertFromRaw(file))
      : EditorState.createEmpty()
  );
  const [files, setFiles] = useState([]);

  const onSubmit = () => {
    if (ttl !== "" && desc !== "") {
      if (type === 0) {
        const obj = {
          courseModule: courseModule,
          title: ttl,
          description: desc,
          type: type,
          visibility: true,
          // max_view_count: maxViewCount,
          view_count: 0,
          file: convertToRaw(content.getCurrentContent()),
        };
        addCourseModuleContent(type, obj, setOpen);
      } else if (type === 1 && files.length > 0) {
        const obj = {
          courseModule: courseModule,
          title: ttl,
          description: desc,
          type: type,
          visibility: true,
          max_view_count: maxViewCount,
          view_count: 0,
          // resolution: [Resolutions[resolution][1], Resolutions[resolution][2]],
          // fps: FPSs[fps],
          file: files,
        };
        addCourseModuleContent(type, obj, setOpen);
      } else if (type === 2 && files.length > 0) {
        const obj = {
          courseModule: courseModule,
          title: ttl,
          description: desc,
          type: type,
          visibility: true,
          view_count: 0,
          // resolution: [Resolutions[resolution][1], Resolutions[resolution][2]],
          // fps: FPSs[fps],
          file: files,
        };
        addCourseModuleContent(type, obj, setOpen);
      }
    }
  };

  const onUpdate = () => {
    if (ttl !== "" && desc !== "") {
      updateCourseModuleContent(
        id,
        { title: ttl, description: desc, max_view_count: maxViewCount },
        setOpen
      );
    }
  };

  return (
    <form className={classes.form} noValidate>
      <Grid container spacing={2}>
        <Grid item xs={12} md={12} lg={12}>
          <TextInput
            label="Title"
            name="title"
            autoFocus={true}
            size="large"
            value={ttl}
            onChange={(e) => setTitle(e.target.value)}
            helperText="The title feild cannot be empty"
          />
        </Grid>
      </Grid>
      <Grid container spacing={2}>
        <Grid item xs={12} md={type === 1 ? 8 : 12} lg={type === 1 ? 8 : 12}>
          <TextInput
            label="Decription"
            name="desc"
            size="large"
            value={desc}
            multiline={true}
            onChange={(e) => setDesc(e.target.value)}
            helperText="The description feild cannot be empty"
          />
        </Grid>
        {type === 1 && (
          <>
            <Grid item xs={4} md={4} lg={4}>
              <SelectMenu
                variant="outlined"
                size="large"
                onChange={(e) => setMaxViewCount(e.target.value)}
                data={maxViewCounts.map((view, i) => ({
                  value: view,
                  label: view,
                }))}
                label="Maximum View Attempts"
                fullWidth={true}
                value={maxViewCount}
              />
            </Grid>
            {/* <Grid item xs={4} md={4} lg={4}>
              <SelectMenu
                variant="outlined"
                size="large"
                onChange={(e) => setResolution(e.target.value)}
                data={Resolutions.map((resol, i) => ({
                  value: i,
                  label: resol[0],
                }))}
                label="Resolution"
                fullWidth={true}
                value={resolution}
                disabled={true}
              />
            </Grid>
            <Grid item xs={4} md={4} lg={4}>
              <SelectMenu
                variant="outlined"
                size="large"
                onChange={(e) => setFPS(e.target.value)}
                data={FPSs.map((fpS, i) => ({
                  value: i,
                  label: fpS,
                }))}
                label="FPS"
                fullWidth={true}
                value={fps}
                disabled={true}
              />
            </Grid> */}
            {/* <Grid item xs={12} md={12} lg={12}>
              <CustomCheckBox
                onChange={(e) => setDownload(e.target.checked)}
                value={download}
                label="Download"
                name="downloadable"
              />
            </Grid> */}
          </>
        )}
      </Grid>

      <Grid container spacing={2}>
        <Grid item xs={12} md={12} lg={12}>
          {action === 0 && type === 1 ? (
            <DropZone
              maxFiles={1}
              type={type}
              title={"Drag 'n' drop some files here, or click to select files"}
              dragActiveTitle={"Drop the files here ..."}
              subtitle={
                "(1 file is the maximum number of files you can drop here)"
              }
              files={files}
              setFiles={setFiles}
              progress={uploadingProgress}
              maxSize={1000000000}
            />
          ) : action === 0 && type === 2 ? (
            <DropZone
              maxFiles={1}
              type={type}
              title={"Drag 'n' drop some files here, or click to select files"}
              dragActiveTitle={"Drop the files here ..."}
              subtitle={
                "(1 file is the maximum number of files you can drop here)"
              }
              files={files}
              setFiles={setFiles}
              progress={uploadingProgress}
              maxSize={10000000}
            />
          ) : type === 0 ? (
            <TextEditor
              value={content}
              onChange={(editor_data) => setContent(editor_data)}
              placeholder="  Enter description"
            />
          ) : null}
        </Grid>
      </Grid>
      {/* <Divider variant="fullWidth" /> */}
      {action === 0 ? (
        <Button
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={onSubmit}
        >
          Add
        </Button>
      ) : (
        <Button
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={onUpdate}
        >
          Update
        </Button>
      )}
    </form>
  );
}
