import { Grid, Hidden } from "@material-ui/core";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import React, { useContext, useState, useEffect } from "react";
import { CourseContext } from "../../context/CourseContext/CourseContext";
import { StudentContext } from "../../context/StudentContext/StudentContext";
import DataTable from "../../ui/DataTable/DataTable";
import SelectMenu from "../../ui/SelectMenu/SelectMenu";
import Paper from "@material-ui/core/Paper";

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: theme.spacing(1),
  },
  fixedHeight: {
    height: "100%",
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
  },
}));

export default function StudentListTable() {
  const classes = useStyles();
  const {
    students,
    // initializing,
    selectedCourses,
    setSelectedCourses,
    updateUserAuthByAdmin,
    // addStudent,
    updateStudent,
    deleteStudent,
  } = useContext(StudentContext);
  const { courses } = useContext(CourseContext);
  const [data, setData] = useState(students);
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

  useEffect(() => {
    console.log(students);
    setData(students);
  }, [students]);

  return (
    <Grid container spacing={3}>
      <Grid item xs={12} md={4} lg={4}>
        <Paper className={fixedHeightPaper}>
          <SelectMenu
            data={
              courses
                ? courses.map((item) => ({
                    value: item.id,
                    label: item.title,
                  }))
                : []
            }
            label="Select Course"
            width={"100%"}
            value={selectedCourses}
            fullWidth={true}
            onChange={(e) => setSelectedCourses(e.target.value)}
            SelectProps={{ multiple: true }}
          />
        </Paper>
      </Grid>
      <Hidden xsDown>
        <Grid item xs={12} md={8} lg={8}></Grid>
      </Hidden>

      <Grid item xs={12} md={12} lg={12}>
        <DataTable
          grouping={true}
          exportButton={true}
          paging={true}
          title={`${
            courses &&
            courses
              .filter((val) => selectedCourses.includes(val.id))
              .map((val) => val.title)
          }`}
          columns={[
            { title: "Username", field: "username" },
            { title: "Name", field: "name" },
            // { title: "Email", field: "email" },
            // { title: "Dob", field: "dob" },
            // { title: "School", field: "school" },
            { title: "NIC", field: "nic" },
            { title: "Contact", field: "contact" },
            { title: "Gender", field: "gender" },
          ]}
          data={
            data
              ? data.map((item) => {
                  item.gender = item.gender === 0 ? "Male" : "Female";
                  return item;
                })
              : []
          }
          editable={{
            // onRowAdd: (newRow) =>
            //   new Promise((resolve) => {
            //     resolve();
            //     setData((prevState) => {
            //       const newData = [...prevState];
            //       newData.push(newRow);
            //       return newData;
            //     });
            //     addStudent(newRow);
            //   }),
            // onRowUpdate: (newData, oldData) =>
            //   new Promise((resolve) => {
            //     resolve();
            //     if (oldData) {
            //       newData.gender = parseInt(newData.gender);
            //       setData((prevState) => {
            //         const updatedData = [...prevState];
            //         updatedData[updatedData.indexOf(oldData)] = newData;
            //         return updatedData;
            //       });
            //       console.log(newData, oldData);
            //       updateStudent(newData.id, newData);
            //     }
            //   }),
            onRowDelete: (oldData) =>
              new Promise((resolve) => {
                resolve();
                setData((prevState) => {
                  const removedData = [...prevState];
                  removedData.splice(removedData.indexOf(oldData), 1);
                  return removedData;
                });
                deleteStudent(oldData.id);
              }),
          }}
          actions={[
            {
              icon: "block",
              tooltip: "Disable User",
              onClick: (event, rowData) => {
                updateUserAuthByAdmin(rowData.id, { disabled: true });
              },
            },
            {
              icon: "vpn_key",
              tooltip: "Enable User",
              onClick: (event, rowData) => {
                updateUserAuthByAdmin(rowData.id, { disabled: false });
              },
            },
            {
              icon: "lock_open",
              tooltip: "Reset password",
              onClick: (event, rowData) => {
                console.log(rowData.id, {
                  password: rowData.username + "@password",
                });
                updateUserAuthByAdmin(rowData.id, {
                  password: rowData.username + "@password",
                });
              },
            },
            {
              icon: "phonelink_lock",
              tooltip: "Device Reset",
              onClick: (event, rowData) => {
                console.log(rowData);
                updateStudent(rowData.id, { device_id: "" });
              },
            },
          ]}
        />
      </Grid>
    </Grid>
  );
}
