import React, { useState } from "react";
import { TextField } from "@material-ui/core";

export default function TextInput(props) {
  const {
    multiline,
    label,
    rows,
    type,
    // error,
    disabled,
    helperText,
    name,
    rowsMax,
    value,
    onChange,
    margin,
    autoFocus,
    size,
    inputProps,
    onInput,
    min,
  } = props;

  const [err, setErr] = useState(false);

  return (
    <TextField
      name={name}
      margin={margin}
      label={label}
      multiline={multiline}
      rows={rows}
      rowsMax={rowsMax}
      value={value}
      type={type}
      autoFocus={autoFocus}
      // placeholder={label}
      autoComplete={false}
      onChange={onChange}
      variant="outlined"
      error={err}
      disabled={disabled}
      helperText={err ? helperText : null}
      size={size}
      fullWidth={true}
      inputProps={inputProps}
      onBlur={() => setErr(value === "")}
      onInput={onInput}
      min={min}
    />
  );
}
