import React, { useContext } from "react";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete";
import EditIcon from "@material-ui/icons/Edit";
import MovieIcon from "@material-ui/icons/Movie";
import PictureAsPdfIcon from "@material-ui/icons/PictureAsPdf";
import TextFieldsIcon from "@material-ui/icons/TextFields";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import VisibilityIcon from "@material-ui/icons/Visibility";
import InsertDriveFileIcon from "@material-ui/icons/InsertDriveFile";
import VisibilityOffIcon from "@material-ui/icons/VisibilityOff";
// import { useHistory, useRouteMatch } from "react-router-dom";
import { Typography } from "@material-ui/core";
import { AuthContext } from "../../../../context/AuthContext/AuthContext";
// import { CourseContext } from "../../../context/CourseContext/CourseContext";

export default function CustomAccordionItem(props) {
  // let { url } = useRouteMatch();
  // const history = useHistory();

  const {
    section,
    visibilitySectionContent,
    item_no,
    title,
    // file,
    // description,
    type,
    view_count,
    max_view_count,
    onEditSectionContent,
    onDeleteSectionContent,
    onHideSectionContent,
    handleGotoCourseModuleContent,
  } = props;

  const { role } = useContext(AuthContext);

  const CourseContentListItem = (
    <ListItem button>
      <ListItemIcon>
        {type === 1 ? (
          <MovieIcon />
        ) : type === 2 ? (
          <PictureAsPdfIcon />
        ) : type === 0 ? (
          <TextFieldsIcon />
        ) : (
          <InsertDriveFileIcon />
        )}
      </ListItemIcon>
      <ListItemText
        primary={title}
        secondary={
          <div
            style={{
              display: "flex",
              justifyContent: "flex-start",
              alignItems: "center",
            }}
          >
            {/* <Typography>{description}</Typography> */}
            {/* <Typography> </Typography> */}
            <Typography style={{ fontStyle: "italic" }}>
              {role === 0 && "Total views: " + view_count}
              {type === 1 && " Maximum View Count: " + max_view_count}
            </Typography>
          </div>
        }
      />

      <ListItemSecondaryAction>
        {/* <Link
to={{
  pathname: `${url}/${title}`,
  state: {
    params: { file: file, description: description, type: type },
  },
}}
> */}
        <IconButton
          onClick={() => handleGotoCourseModuleContent(item_no)}
          aria-label="share"
        >
          <ArrowForwardIcon />
        </IconButton>
        {/* </Link> */}
        {role === 0 && (
          <>
            <IconButton
              onClick={() => {
                onHideSectionContent(section, item_no);
              }}
              edge="end"
              aria-label="edit"
            >
              {visibilitySectionContent ? (
                <VisibilityIcon />
              ) : (
                <VisibilityOffIcon />
              )}
            </IconButton>
            <IconButton
              onClick={() => onEditSectionContent(section, item_no)}
              edge="end"
              aria-label="edit"
            >
              <EditIcon />
            </IconButton>
            <IconButton
              onClick={() => onDeleteSectionContent(section, item_no, type)}
              edge="end"
              aria-label="delete"
            >
              <DeleteIcon />
            </IconButton>
          </>
        )}
      </ListItemSecondaryAction>
    </ListItem>
  );

  return (
    <>
      {role === 1 && visibilitySectionContent
        ? CourseContentListItem
        : role === 0
        ? CourseContentListItem
        : null}
    </>
  );
}
