import React, { useContext, useState } from "react";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
// import { useHistory } from "react-router-dom";
import SelectMenu from "../../ui/SelectMenu/SelectMenu";
import TextInput from "../../ui/TextInput/TextInput";
import CustomDatePicker from "../../ui/CustomDatePicker/CustomDatePicker";
import { AuthContext } from "../../context/AuthContext/AuthContext";
// import { Typography } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

export default function StudentRegisterForm(props) {
  const classes = useStyles();

  const { signUp, profile, updateUser, updateUserCredentials } = useContext(
    AuthContext
  );
  const { action, setOpen } = props;
  const [fname, setFname] = useState(
    action === 1 && profile && profile.name ? profile.name.split(" ")[0] : ""
  );
  const [lname, setLname] = useState(
    action === 1 && profile && profile.name ? profile.name.split(" ")[1] : ""
  );
  const [uname, setUname] = useState(
    action === 1 && profile && profile.username ? profile.username : ""
  );
  const [contact, setContact] = useState(
    action === 1 && profile && profile.contact ? profile.contact : ""
  );
  const [nic, setNic] = useState(
    action === 1 && profile && profile.nic ? profile.nic : ""
  );
  const [gender, setGender] = useState(
    action === 1 && profile && profile.gender ? profile.gender : 0
  );

  const dateNow = new Date();
  dateNow.setFullYear(dateNow.getFullYear() - 15);
  const [dob, setDob] = useState(
    action === 1 && profile && profile.dob
      ? new Date(profile.dob.toDate())
      : dateNow
  );
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  let OnSubmit = (e) => {
    e.preventDefault();
    console.log("reg");
    if (
      uname !== "" &&
      fname !== "" &&
      lname !== "" &&
      nic !== "" &&
      password !== "" &&
      contact !== "" &&
      password === confirmPassword
    ) {
      const obj = {
        username: uname,
        name: fname + " " + lname,
        nic: nic,
        gender: gender,
        dob: dob,
        email: uname + ".dayalherath@gmail.com",
        password: password,
        contact: contact,
        role: 1,
        device_id: "",
      };

      signUp(obj);
    }
  };

  let OnUpdateProfile = (e) => {
    if (fname !== "" && lname !== "") {
      const obj = {
        name: fname + " " + lname,
        dob: dob,
      };
      updateUser(obj, setOpen);
    }
  };
  let OnUpdatePassword = (e) => {
    updateUserCredentials(password, setOpen);
  };

  return (
    <form onSubmit={OnSubmit} className={classes.form} noValidate>
      <Grid container spacing={2}>
        <Grid item xs={6} md={6} lg={6}>
          <TextInput
            value={fname}
            onChange={(e) => setFname(e.target.value)}
            label="First Name"
            name="fname"
            helperText={"Ex: Anuhas"}
            autoFocus={true}
          />
        </Grid>
        <Grid item xs={6} md={6} lg={6}>
          <TextInput
            value={lname}
            onChange={(e) => setLname(e.target.value)}
            label="Last Name"
            name="lname"
            helperText={"Ex: Attanayake"}
          />
        </Grid>
      </Grid>
      <Grid container spacing={2}>
        <Grid item xs={12} md={12} lg={12}>
          <TextInput
            value={uname}
            onChange={(e) => setUname(e.target.value)}
            label="Username"
            name="uname"
            disabled={action === 1}
            helperText={"Ex: 20210001"}
          />
        </Grid>
      </Grid>
      <Grid container spacing={2}>
        <Grid item xs={6} md={6} lg={6}>
          <TextInput
            value={contact}
            onChange={(e) => setContact(e.target.value)}
            label="Contact Number"
            name="contact"
            type="number"
            disabled={action === 1}
            helperText={"Ex: 0771234567"}
            // inputProps={{ pattern: "0{1}7{1}[0-9]{1}[0-9]{7}" }}
            // onInput={(e) => {
            //   e.target.value = Math.max(-1, parseInt(e.target.value))
            //     .toString()
            //     .slice(0, 10);
            // }}
          />
        </Grid>
        <Grid item xs={6} md={6} lg={6}>
          <TextInput
            value={nic}
            onChange={(e) => setNic(e.target.value)}
            label="National Identity Card"
            name="nic"
            disabled={action === 1}
            helperText={"Ex: 200212345678"}
          />
        </Grid>
      </Grid>

      <Grid container spacing={2}>
        <Grid item xs={6} md={6} lg={6}>
          <CustomDatePicker
            value={dob}
            onChange={(e) => setDob(e)}
            size="large"
            label="Date of Birth"
            maxDate={dateNow}
          />
        </Grid>
        <Grid item xs={6} md={6} lg={6}>
          <SelectMenu
            variant="outlined"
            size="large"
            onChange={(e) => setGender(e.target.value)}
            data={[
              { value: 0, label: "Male" },
              { value: 1, label: "Female" },
            ]}
            label="Gender"
            fullWidth={true}
            value={gender}
            disabled={action === 1}
          />
        </Grid>
      </Grid>

      <Grid container spacing={2}>
        <Grid
          item
          xs={action === 1 ? 4 : 6}
          md={action === 1 ? 4 : 6}
          lg={action === 1 ? 4 : 6}
        >
          <TextInput
            type="password"
            label={action === 1 ? "New Password" : "Password"}
            name="pwd"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </Grid>
        <Grid
          item
          xs={action === 1 ? 4 : 6}
          md={action === 1 ? 4 : 6}
          lg={action === 1 ? 4 : 6}
        >
          <TextInput
            type="password"
            label="Confirm Password"
            name="cpwd"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
          />
        </Grid>
        {action === 1 && (
          <Grid item xs={12} md={4} lg={4}>
            <Button
              fullWidth
              variant="contained"
              color="secondary"
              style={{ height: "100%" }}
              onClick={OnUpdatePassword}
            >
              Update Password
            </Button>
          </Grid>
        )}
      </Grid>

      {action === 0 ? (
        <Button
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          // onClick={OnSubmit}
          type="submit"
        >
          Register Now
        </Button>
      ) : (
        <Button
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={OnUpdateProfile}
        >
          Update Profile
        </Button>
      )}
    </form>
  );
}
