import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import app, { firebase } from "../../firebase/firebaseConfig";
import { useCookies } from "react-cookie";
import { useSnackbar } from "notistack";

export const AuthContext = React.createContext({});

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(false);
  const [initializing, setInitializing] = useState(true);
  const [institute, setInstitute] = useState("I5BYztckUrUdJctSewjD");
  const [instituteData, setInstituteData] = useState(null);
  const [role, setRole] = useState(null);
  const [profile, setProfile] = useState(null);
  const [userPayments, setUserPayments] = useState(null);
  const [error, setError] = useState(null);
  const [isSignIn, setIsSignIn] = useState(false);
  const [isSignUp, setIsSignUp] = useState(false);
  const history = useHistory();
  const [cookies, setCookie, removeCookie] = useCookies(["device_id"]);
  // const location = useLocation();
  const { enqueueSnackbar } = useSnackbar();

  useEffect(() => {
    app.auth().onAuthStateChanged(async (currentUser) => {
      setInitializing(true);
      if (currentUser) {
        setUser(currentUser);
        const instituteDetails = await getInstitute();
        setInstitute(instituteDetails.id);
        setInstituteData(instituteDetails);
        console.log(!isSignIn && !isSignUp);
        if (!isSignIn && !isSignUp) {
          const userDetails = await getUser(currentUser);
          setRole(userDetails.role);
          setProfile(userDetails);
          const userPaymentDetails = await getUserPayment(currentUser);
          setUserPayments(userPaymentDetails);
          if (userDetails.role === 1 && userPaymentDetails.length === 0) {
            enqueueSnackbar(
              "Oops! Please complete payments for the month or contact a site administrator",
              {
                variant: "error",
                autoHideDuration: 60000,
              }
            );
          }
        }
      } else {
        setUser(null);
      }
      setInitializing(false);
    });
  }, []);

  const signIn = async (email, password) => {
    try {
      setIsSignIn(true);
      const response = await app
        .auth()
        .signInWithEmailAndPassword(email, password);
      const userDetails = await getUser(response.user);
      setRole(userDetails.role);
      setProfile(userDetails);
      const userPaymentDetails = await getUserPayment(response.user);
      setUserPayments(userPaymentDetails);
      if (userDetails.role === 1 && userPaymentDetails.length === 0) {
        enqueueSnackbar(
          "Oops! Please complete payments for the month or contact a site administrator",
          {
            variant: "error",
            autoHideDuration: 60000,
          }
        );
      }
      await checkDeviceId(
        response.user.uid,
        userDetails.device_id,
        userDetails.role
      );
      setIsSignIn(false);
    } catch (e) {
      setIsSignIn(false);
      setError(e.message);
      enqueueSnackbar(e.message, { variant: "error" });
      console.log(e.message);
    }
  };

  const updateUser = (userObj, setOpen) => {
    setLoading(true);
    if (userObj.dob) {
      userObj.dob = firebase.firestore.Timestamp.fromDate(userObj.dob);
    }
    app
      .firestore()
      .collection("users")
      .doc(user.uid)
      .update(userObj)
      .then(async (obj) => {
        enqueueSnackbar("Document written successfully!", {
          variant: "success",
        });
        console.log(obj);
        setOpen(false);
        setLoading(false);
        const userDetails = await getUser(user);
        setProfile(userDetails);
      })
      .catch((e) => {
        enqueueSnackbar(e.message, { variant: "error" });
        setLoading(false);
      });
  };

  const updateUserCredentials = (newPassword, setOpen) => {
    setLoading(true);
    user
      .updatePassword(newPassword)
      .then(function () {
        // Update successful.
        enqueueSnackbar("Update successful!", { variant: "success" });
        setOpen(false);
      })
      .catch((e) => {
        // An error happened.
        enqueueSnackbar(e.message, { variant: "error" });
      });
  };

  const checkDeviceId = async (user_id, device_id, user_role) => {
    if (user_role === 0) {
      history.push("/main/dashboard");
    } else {
      if (device_id === "") {
        await updateUserCookie(user_id);
      } else {
        if (device_id === cookies.device_id) {
          console.log(device_id, cookies.device_id);
          history.push("/main/dashboard");
        } else {
          setError("Device verification failed");
          enqueueSnackbar("Device verification failed", { variant: "error" });
        }
      }
    }
  };

  const signUp = async (userObj) => {
    try {
      setError(null);
      setIsSignUp(true);
      const { email, password } = userObj;
      const response = await app
        .auth()
        .createUserWithEmailAndPassword(email, password);
      const uid = response.user.uid;
      userObj.dob = firebase.firestore.Timestamp.fromDate(userObj.dob);
      await createUser({ id: uid, ...userObj });
      const userDetails = await getUser(response.user);
      console.log(userDetails);

      setRole(userDetails.role);
      setProfile(userDetails);
      const userPaymentDetails = await getUserPayment(response.user);
      console.log(userPaymentDetails);
      setUserPayments(userPaymentDetails);
      if (userDetails.role === 1 && userPaymentDetails.length === 0) {
        enqueueSnackbar("Please complete payments for the month", {
          variant: "error",
        });
      }
      await checkDeviceId(
        response.user.uid,
        userDetails.device_id,
        userDetails.role
      );
    } catch (e) {
      setError(e.message);
      enqueueSnackbar(e.message, { variant: "error" });
    }
  };

  const createUser = async (userObj) => {
    delete userObj.password;
    try {
      await app
        .firestore()
        .collection("users")
        .doc(userObj.id)
        .set({ institute: institute, ...userObj });
      // history.push("/main/dashboard");
    } catch (e) {
      setError(e.message);
      enqueueSnackbar(e.message, { variant: "error" });
    }
  };

  const getUser = async (new_user) => {
    try {
      console.log("2.1");
      const response = await app
        .firestore()
        .collection("users")
        .doc(new_user.uid)
        .get();
      console.log("2.2");
      const data = await response.data();
      return data;
    } catch (e) {
      setError(e.message);
      console.log(e.message);
      enqueueSnackbar(e.message, { variant: "error" });
    }
  };

  const getUserPayment = async (new_user) => {
    try {
      const date = new Date();
      const month = date.getMonth() + 1;
      const year = date.getFullYear();
      const today = date.getDate();
      const previousMonth = month === 1 ? 12 : month - 1;

      const response = await app
        .firestore()
        .collection("payments")
        .where("user", "==", new_user.uid)
        .where("month", "in", [previousMonth, month])
        .where("year", "==", year)
        .get();

      const arr = [];
      for (let doc of response.docs) {
        arr.push({ id: doc.id, ...doc.data() });
      }
      if (month === 1) {
        console.log("Month 1");
        const responsePrevYear = await app
          .firestore()
          .collection("payments")
          .where("user", "==", new_user.uid)
          .where("month", "in", [previousMonth])
          .where("year", "==", year - 1)
          .get();

        for (let doc of responsePrevYear.docs) {
          arr.push({ id: doc.id, ...doc.data() });
        }
      }
      const paidMonths = arr.filter((val) => val.month === month);
      const paidPrevMonths = arr.filter((val) => val.month === month - 1);
      if (today > 15) {
        console.log("Today>15", paidMonths);
        return paidMonths;
      } else {
        console.log(
          "Today<=15",
          paidMonths.length,
          paidMonths.length > 0 ? paidMonths : paidPrevMonths
        );
        return paidMonths.length > 0 ? paidMonths : paidPrevMonths;
      }
    } catch (e) {
      setError(e.message);
      console.log(e.message);
      enqueueSnackbar(e.message, { variant: "error" });
    }
  };

  const uuidv4 = () => {
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
      /[xy]/g,
      function (c) {
        var r = (Math.random() * 16) | 0,
          v = c === "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      }
    );
  };

  const updateUserCookie = async (user_id) => {
    try {
      const deviceId = uuidv4();
      await app
        .firestore()
        .collection("users")
        .doc(user_id)
        .update({ device_id: deviceId });
      removeCookie("device_id");
      setCookie("device_id", deviceId, {
        maxAge: 7890000,
        path: "/",
      });
      history.push("/main/dashboard");
    } catch (e) {
      setError("Error writing document: ", e.message);
      console.error("Error writing document: ", e.message);
      enqueueSnackbar(e.message, { variant: "error" });
    }
  };

  const getInstitute = async () => {
    try {
      const response = await app
        .firestore()
        .collection("institutes")
        .doc(institute)
        .get();
      const data = await response.data();
      console.log(data);
      return { id: response.id, ...data };
    } catch (e) {
      setError(e.message);
      console.log(e.message);
      enqueueSnackbar(e.message, { variant: "error" });
    }
  };

  const signOut = () => {
    app
      .auth()
      .signOut()
      .then(() => {
        setUser(null);
        history.push("/");
      })
      .catch((e) => {
        setError(e.message);
        enqueueSnackbar(e.message, { variant: "error" });
      });
  };

  return (
    <AuthContext.Provider
      value={{
        user,
        error,
        role,
        profile,
        userPayments,
        institute,
        instituteData,
        initializing,
        loading,
        signIn,
        signOut,
        signUp,
        updateUser,
        updateUserCredentials,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};
