import React, { useContext } from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import Drawer from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import { Switch, Route, useHistory } from "react-router-dom";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import DashboardIcon from "@material-ui/icons/Dashboard";
import MenuBookOutlinedIcon from "@material-ui/icons/MenuBookOutlined";
import GroupOutlinedIcon from "@material-ui/icons/GroupOutlined";
import CreditCard from "@material-ui/icons/CreditCard";
// import { Avatar, Typography } from "@material-ui/core";

import Dashboard from "./Dashboard/Dashboard";
import Payment from "./Payment/Payment";
import Student from "./Student/Student";
import Courses from "./Courses/Courses";
import { AuthContext } from "../../../context/AuthContext/AuthContext";
import { CourseProvider } from "../../../context/CourseContext/CourseContext";
import { StudentProvider } from "../../../context/StudentContext/StudentContext";
import { PaymentProvider } from "../../../context/PaymentContext/PaymentContext";

const routes = [
  {
    path: "/main/dashboard",
    main: () => <Dashboard />,
    // exact: true,
  },
  {
    path: "/main/courses",
    main: () => <Courses />,
  },
  {
    path: "/main/student",
    main: () => <Student />,
    // exact: true,
  },
  {
    path: "/main/payment",
    main: () => <Payment />,
    // exact: true,
  },
];

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  toolbarIcon: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-start",
    padding: "0 8px",
    ...theme.mixins.toolbar,
  },
  drawerPaper: {
    position: "relative",
    whiteSpace: "nowrap",
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: "hidden",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9),
    },
  },
}));

export default function AppSidebar(props) {
  const classes = useStyles();
  const history = useHistory();
  const { role } = useContext(AuthContext);
  console.log(role);
  return (
    <React.Fragment>
      <Drawer
        variant="permanent"
        classes={{
          paper: clsx(
            classes.drawerPaper,
            !props.open && classes.drawerPaperClose
          ),
        }}
        open={props.open}
      >
        <div className={classes.toolbarIcon}>
          {/* <Avatar
            alt="Remy Sharp"
            src={{ url: instituteData.sidebar_upper_image }}
          /> */}
          {/* <Typography variant="h5" component="h4" align="left">
            {instituteData && instituteData.owner_name}
          </Typography> */}
          <div style={{ flexGrow: 1 }} />
          <IconButton onClick={props.handleDrawerClose}>
            <ChevronLeftIcon />
          </IconButton>
        </div>
        <Divider />
        <List>
          <ListItem button onClick={() => history.push("/main/dashboard")}>
            <ListItemIcon>
              <DashboardIcon />
            </ListItemIcon>
            <ListItemText primary="Dashboard" />
          </ListItem>

          <ListItem button onClick={() => history.push("/main/courses")}>
            <ListItemIcon>
              <MenuBookOutlinedIcon />
            </ListItemIcon>
            <ListItemText primary="Courses" />
          </ListItem>
          {role === 0 && (
            <ListItem button onClick={() => history.push("/main/student")}>
              <ListItemIcon>
                <GroupOutlinedIcon />
              </ListItemIcon>
              <ListItemText primary="Students" />
            </ListItem>
          )}

          {role === 0 && (
            <ListItem button onClick={() => history.push("/main/payment")}>
              <ListItemIcon>
                <CreditCard />
              </ListItemIcon>
              <ListItemText primary="Payment" />
            </ListItem>
          )}
        </List>
      </Drawer>

      <Switch>
        <CourseProvider>
          <StudentProvider>
            <PaymentProvider>
              {routes.map((route, index) => (
                <Route
                  key={index}
                  path={route.path}
                  exact={route.exact}
                  children={<route.main />}
                />
              ))}
            </PaymentProvider>
          </StudentProvider>
        </CourseProvider>
      </Switch>
    </React.Fragment>
  );
}
