import React from "react";
import MaterialTable from "material-table";

export default function DataTable(props) {
  const {
    columns,
    data,
    title,
    search,
    grouping,
    exportButton,
    paging,
    selection,
    sorting,
    editable,
    actions,
  } = props;

  return (
    <MaterialTable
      style={{ width: "100%" }}
      title={title}
      columns={columns}
      data={data}
      options={{
        search: search,
        grouping: grouping,
        exportButton: exportButton,
        actionsColumnIndex: -1,
        paging: paging,
        selection: selection,
        sorting: sorting,
      }}
      editable={editable}
      actions={actions}
    />
  );
}
